.Search{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left: 100px;
}

.Search input{
    width: 80%;
    height: 30px;
    font-size: 20px;
}

:root{
    --card-bg-color: pink;
}

.card-container{
    background-color: pink;
    background-color: var(--card-bg-color);
    display: grid;
    grid-template-columns: 4fr 1fr;
    width: 25rem;;
    height: 20rem;
    /* margin-left: auto; */
    border-radius: 1rem;
}

.img-container{
    min-height: 100%;
    min-width: 100%;

    overflow: hidden;
    border-radius: 1rem;
}

.card-image{
    height: 100%;
    width: auto;
}

.card-body{
    width: 100%;
    overflow: hidden;
}

.card-text{
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    height: -webkit-min-content;
    height: min-content;
}

.card-text h5{
    width: -webkit-max-content;
    width: max-content;
}

.title {
    font-family: Georgia, 'Times New Roman', Times, serif;
    padding: 0px;
    margin: 0px;
  }

.subtitle {
    font-family: Georgia, 'Times New Roman', Times, serif;
    padding: 0px;
    margin: 0px;
}

Header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

.card-deck {
    background-color: Gray;
    width: 100%;
    min-height: 10em;
    max-height: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: row;
    grid-auto-rows: minmax(1em, auto);
    grid-gap: 1em;
    gap: 1em;
    margin-left: 13rem;
  }

#Log {
    display: flex;
    flex-direction: column;
  }


:root{
  --pink-gradient-start: #fb61ba;
  --pink-gradient-end: #ff839d;
  --blue-gradient-start: #459bdb;
  --blue-gradient-end: #6f12e0;

  --sidebar-inactive: #f6e9ef;
  --sidebar-active: #fff5f7;
}

.sidebar {
    display: block;
    flex-direction: column;

    position: fixed;
    left: 0;
    top: 25%;

    margin: 0;
    height: -webkit-max-content;
    height: max-content;
    width: 12rem;
    justify-content: center;

    background-color: #fb61ba;

    background-color: var(--pink-gradient-start);
    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;

    box-shadow: 0rem 0rem 0.5rem 0.1rem #88888822;
}

.sidebar-item-container{
    display: flex;
    flex-direction: column;

    height: 100%;
    background-color: #f6e9ef;
    background-color: var(--sidebar-inactive);
    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;
}
 
.sidebar-item {
    display: flex;
    flex-direction: column;
    padding: 0.5rem 0;
    margin: 0;
    text-align: left;
    color: inherit;
    text-decoration: inherit;
    outline: none;
    height: -webkit-min-content;
    height: min-content;

    box-shadow: 0rem 1rem 1rem -0.6rem #88888822;
}

.sidebar-item.active{
    height: 100%;
    width: 100%;
    background-color: #fff5f7;
    background-color: var(--sidebar-active);

    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
}

.sidebar-listheader{
    margin: 0;
    width: 100%;
    text-align: inherit;
    padding: 0.5rem 0;
    padding-left: 1rem;
}

.active .sidebar-listheader{
    width: 12rem;
    display: block;
    color: white;
    background-color: #459bdb;
    background-color: var(--blue-gradient-start);

    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;
}

.list-items-container{
    margin-top: 0.5rem;
    margin-left: 1rem;
    display: none;
    overflow: hidden;
}

.active .list-items-container{
    height: 100%;

    display: flex;
    flex-direction: column;
}

.list-item{
    margin: 0.25rem;
}

.social-media-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 0 auto;
  place-items: center;
}

.social-media-link{
  color: white;
  font-size: 2rem;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

